import React from 'react';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';

export default class OrgsTable extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      numRows: 1,
    } 
  }

  render() {
    const rows = [];
    for (let i = 0; i < this.state.numRows; i++){
      rows.push(this.renderRow());
    }

    return (
      <div>
        <table>
          <tr>
            <td class='wide'>Influencer</td>
            <td>Ranking?</td>
            <td>Please explain your ranking.</td>
            <td>If this influencer had not existed, how likely do you think it is that the same effect on you would have happened anyway? (Please answer with a %, so if you thought the probability was 0.5 you'd answer "50.")</td>
            <td>Please explain your percentage.</td>
          </tr>

          {rows}

          <tr>
            <button onClick={() => this.setState({ numRows: this.state.numRows + 1 })}>
              Add Another
            </button>
          </tr>
        </table>
      </div>
    );
  }

  renderRow() {
    return (
      <tr>
        <td class='wide'>{this.renderAutocompleteBox()}</td>
        <td>
          <select>
            <option></option>
            <option>One of the top 3 I'm putting here</option>
            <option>Impactful, but not top 3</option>
          </select>
        </td>
        <td>
          <textarea></textarea>
        </td>
        <td>
          <input type='number'/>
        </td>
        <td>
          <textarea></textarea>
        </td>
      </tr>
    );
  }

  renderAutocompleteBox() {    
    return (
      <Autocomplete
        freeSolo
        options={orgs.map((option) => option.title)}
        renderInput={(params) => (
          <TextField {...params} label="Click me..." margin="normal" variant="outlined" />
        )}
      />
    );
  }
}

const orgs = [
  { title: '80,000 Hours' },
  { title: 'Centre for Effective Altruism ' },
  // { title: 'Center for Long Term Risk (previously, the Effective Altruism Foundation. Include the work of the Foundational Research Institute and Raising for Effective Giving) ' },
  { title: 'Center for Applied Rationality ' },
  { title: 'European Summer Program on Rationality' },
  { title: 'Foundational Research Institute' },
  { title: 'Future of Humanity Institute' },
  { title: 'GiveWell' },
  { title: 'Giving What We Can' },
  { title: 'LessWrong 2.0 (LessWrong since 2017)' },
  { title: 'Machine Intelligence Research Institute' },
  { title: 'Open Philanthropy' },
  { title: 'Summer Program on Applied Rationality and Cognition' },
];
